.canvas-wrapper {
  display: flex;
  justify-content: center;  
  align-items: center;      
  flex-grow: 1;
  min-height: 600px;
}
.stage{
  border: 3px solid #c7c7c7;
  border-radius: 6px;
  background-color: rgb(255, 255, 255);
  margin: 0 auto;
  padding: 0;
}
html, body {
  overflow-x: hidden;
  overflow-y: auto;
  scrollbar-gutter: stable;
}